<template >
  <mt-picker :slots="item.slots" @change="onValuesChange"></mt-picker>
</template>

<script>
export default {
  props:{
    item:Object,
    value:Number
  },
  data (){
    return {

    }
  },
  methods:{
    onValuesChange(picker, values) {
      this.$emit('input', values[0]);
    }
  }
}
</script>

<style lang="css">
</style>



