<template>
	<div>
		<mt-header fixed title="管理病例">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="/uploder" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25">
  		  </router-link>
  		</mt-header>
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  		<div style="margin-top:49px;width:98%;margin-left:1%;">
			<mt-search v-model="value" ></mt-search>
			<p><img src="../../assets/hot.png" alt=""style='vertical-align: middle;'>关键字</p>
			<div>
				<button v-for="button in buttons" class="btn_s">{{button.name}}</button>
			</div>
			<!-- 新增按钮 -->
			<div class="next">
				<router-link to="/uploder" slot="right">
					<mt-button class="next-btn" @click="dialogVisible = true">创建新病例</mt-button>
  		  		</router-link>
			</div>
			<div v-for='group in groups' class="group_name">
				<div>{{group.record_date}}</div>
				<div class="item_name">
					<div v-for='item in group.record_content' >
						<img v-bind:src="path+item.image_path">
						<div>{{item.name}}</div>
					</div>
				</div>
			</div>
			<!-- end -->
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			var buttons = [{name:'体检报告'},{name:'CT'},{name:'B超'},{name:'血常规'}];
			this.getKill();
			return {
				value:null,
				buttons:buttons,
				sheetVisible: false,
				actions: [],
				groups:[],
				path: this.$store.state.serverJS
			}
		},
		methods:{
			getKill(){
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records");
				resource.get({user_id:2}).then((response)=>{
					
					this.groups = response.data;
				},(error)=>{

				})
			}
		},
	}
</script>
<style>
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 9vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s{
		margin-right: 3%;
		border-radius: 4px;
		background: #fff;
		height: 41px;
		padding: 2% 5%;
		border-style: none;
		border: 1px solid #d3dce6;
		box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
	}
	.group_name{
		margin: 10px 10px;
        font-size: 25px;
	}
	.item_name{
		font-size: 16px;
	}
	.item_name div{
		display: inline-block;
		width: 24%;
		margin-right: 2px;
	}
	.item_name div>div{
		width:100%;
		line-height:17px;
		text-align:center;
	}
	.item_name img{
		width:100%;
		height: 60px;
		border:1px solid #000;
	}
</style>


