<template>
	<div>
		<mt-header fixed title="照片预览">
  		  <router-link to="/mge_caese" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		</mt-header>
  		<div style="margin-top:50px;line-height: 36px;">
  			<el-row :gutter="20" style="margin:0;">
	  		  <el-col :span="4">名称</el-col>
	  		  <el-col :span="20">
	  		  	<div><input type="text" class='name_t' v-model="caese_name"></div>
	  		  </el-col>
	  		</el-row>
	  		<el-row :gutter="20" style="margin:0;"> 
	  		  <el-col :span="4">分类</el-col>
	  		  <el-col :span="20">
	  		  	<div><el-select v-model="caese_kinds" filterable allow-create placeholder="请选择">
				    <el-option
				      v-for="item in kinds"
				      :label="item"
				      :value="item">
				    </el-option>
				  </el-select>
				  </el-select></div>
	  		  </el-col>
	  		</el-row>
  			<!-- <el-upload
  			  action="//jsonplaceholder.typicode.com/posts/"
  			  type="drag"
  			  :thumbnail-mode="true"
  			  :on-preview="handlePreview"
  			  :on-remove="handleRemove"
  			  :default-file-list="fileList"
  			>
  			  <i class="el-icon-upload"></i>
  			  <div class="el-dragger__text"><em>点击上传</em></div>
  			</el-upload> -->

  			<!-- <button @click="wx_choose()" class="upload_btn">选择</button> -->
  			<div>
  				<img v-bind:src="images.localIds[0]" alt="" class="choose_img" @click="wx_choose()" >
  			</div>
  			<button class="upload_btn" @click="wx_uploder()">上传</button>

  		</div>
	</div>
</template>
<script>
	const wx = require('weixin-js-sdk')
    var images = {
	        localIds: [],
	        serverId: []
			 }
	
	export default{
		data(){
			this.getKinds();
			return{
              images:images,
              kinds: [],
			  value:'',
			  caese_kinds:[],
			  caese_name:'',
			  localId: images.localIds[0]||''
			}
		},
		methods:{
			handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
	      	handlePreview(file) {
	        	console.log(file);
	      	},
	      	getKinds(){
	      		var resource = this.$resource(this.$store.state.serverIP +"/commons/categorys");
	      		resource.get().then((response)=>{
	      			this.kinds = response.data
	      		})
	      	},
	      	wx_choose(){
	      		wx.ready(function(){
		      			alert("调用微信SDK成功")
			      		wx.checkJsApi({
					      jsApiList: [
					        'chooseImage',
					        'uploadImage',
					        'getLocation'
					      ],
					      success: function (res) {
					        console.log(JSON.stringify(res));
					      }
					    });
		      		    wx.error(function(res){
		      				alert("调用微信SDK失败"+JSON.stringify(res))
						});
						// wx.getLocation({
						//     type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
						//     success: function (res) {
						//         alert("调用微信SDKgetLocation"+JSON.stringify(res));
						//     }
						// });
						// wx.hideOptionMenu();
						wx.chooseImage({
						    count: 1, // 默认9
						    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
						    sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
						    success: function (res) {
						        images.localIds= res.localIds; // localIds是个数组，可以选择多张图片
						    }
						});

					});
	      	},
	      	wx_uploder(){
	      		var _this = this;
	      		wx.ready(function(){
	      			alert("localIDS"+JSON.stringify(images));
	      			wx.error(function(res){
	      				alert("调用微信SDK失败"+JSON.stringify(res))
					});
	      			wx.uploadImage({
					    localId: images.localIds[0], // 需要上传的图片的本地ID，由chooseImage接口获得
					    isShowProgressTips: 1, // 默认为1，显示进度提示
					    success: function (res) {
					    	alert("调用微信SDK上传后"+JSON.stringify(res))
					    	// TODO 微信返回serverId后，通过接口在服务端拉下图片。
					        images.serverId=res.serverId; // 返回图片的服务器端ID
					        alert("参数:"+"caese_name:"+_this.caese_name+"\ncaese_kinds:"+_this.caese_kinds+"\n"+images.serverId);
					        alert("POST resource URL"+_this.$store.state.serverIP);
					        var resource = _this.$resource(_this.$store.state.serverIP +"/medical_record_managements/add_medical_record_management");


				      		resource.save({name:_this.caese_name,category:_this.caese_kinds,image_path:images.serverId,user_id:2}).then((response)=>{
				      			alert("上传数据成功"+JSON.stringify(response.data))
				      		},(err)=>{
				      			alert("上传数据失败"+JSON.stringify(err));

				      		})
					    }
					});
	      		})
	      	}
		},
		mounted:function(){
			// alert(111);
			// debugger
			var resource = this.$resource(this.$store.state.serverJS+'/get_api_tickets');
			// 获取当前路径并生成签名##FIXME
	      		resource.save({url:this.$store.state.serverJS+"app?"+location.search.substring(1).split("&")[0]}).then((response)=>{
	      			wx.config({
	      				// debug: true,
	      				appId: 'wxd7dcf2bd4d9c9617',
	      				timestamp: response.data.timestamp,
	      				nonceStr: response.data.noncestr,
	      				signature: response.data.signature,
	      				jsApiList: ['uploadImage','chooseImage','previewImage',
	      				'getLocation']

	      			})
	      			
	      		})
		}
	}
</script>
<style>
	.name_t{
		border: 1px solid #c0ccda;
		height: 30px;border-radius: 4px;
		margin-bottom: 20px;
	}
	.upload_btn{
		width: 90%;
	    margin-left: 5%;
	    padding: 10px;
	    font-size: 18px;
	    border-radius: 14px;
	    border-style: none;
	    background: #A21A28;
	    color: #fff;
	    font-weight: bold;
	    margin-top: 40px;
	}
	.choose_img{
		width: 96%;
		height: 280px;
		border:1px solid #c0ccda;
		margin-left: 2%;
		margin-top: 25px;
	}
</style>


