<template>
  <div class="summary">
    <div class="summary-title">
      <span class="current">
        {{current_item.name || ""}}
      </span>
      <div class="select" @click="popupVisible=true">
        查看更多
        <i class="iconfont icon-guanzhu"></i>
      </div>
    </div>
    <div class="select-item" v-for="item in healthList">
      {{item}}
    </div>
    <div class="warp">

    </div>
    <mt-popup  style="width:100%;height:500px;"
      v-model="popupVisible"
      position="bottom"
      >
      <div
      class="select-item"
      :class="{'selected':item.id == current_item.id}"
       v-for="item in healthList"
      @click="current_item=item;popupVisible=false;">
        {{item.name}}
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
export default {
  data (){
    return {
      popupVisible:false,
      healthList:[],
      current_item:{}
    }
  },
  methods:{
    getHealthitem (){
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?user_id=2&status=0");
      resource.get().then((callback) => {
        this.healthList = callback.data
        if(this.healthList.length>0){
          this.current_item = this.healthList[0]
        }
      });
    },
  },
  watch:{
    current_item:function (item){
      var resource = this.$resource(this.$store.state.serverIP +"/health_item_records/item_records?health_item_id="+item.id);
      resource.get().then((callback) => {
        Indicator.close();
        this.healthList = callback.data
        if(this.healthList.length>0){
          this.current_item = this.healthList[0]
        }
      },error=>{
        Indicator.close();
      });
    }
  },
  mounted () {
    Indicator.open('加载中...');
    this.getHealthitem()
  }
}

</script>

<style scoped lang="scss">
.summary{
  .summary-title{
    padding:10px 0px;
    border-bottom: 1px solid #ccc;
    overflow: hidden;
    .current{
      height: 26px;
      line-height: 26px;
      padding: 0px 10px;
      color: red;
      float: left;
      border-radius: 5px;
      border:1px solid red
    }
    .select{
      height: 26px;
      line-height: 26px;
      padding: 0px 10px;
      color: #323232;
      float: left;
      margin-left: 20px;
      border-radius:13px;
      border:1px solid #323232
    }
  }
  .mint-popup{
    background: rgba(0,0,0,.5);
    .v-modle{
      background: transparent;
    }
  }
  .select-item{
    margin-left: 15px;
    margin-top: 15px;
    height: 26px;
    line-height: 26px;
    padding: 0px 10px;
    color: #fff;
    float: left;
    border-radius: 5px;
    border:1px solid #fff
  }
  .selected{
    color: red;
    border:1px solid red

  }
}
</style>



